<template>
	<div class="swiper-container" :class='swipeid'><!--  :class='swipeid'banner -->
		<div class="swiper-wrapper">
			<slot name='swiper-con'></slot>
		</div>

		<div :class='{"swiper-pagination":bol}' :style='{"text-align":str}'></div><!--  bol str -->		
	</div>
</template>


<script type="text/javascript">
	
	import '../assets/libs/swiper/js/swiper.js'

	export default{
		props:{
			swipeid:{
				type:String,
				default:""
			},
			bol:{
				type:Boolean,
				default:false
			},
			str:{
				type:String,
				default:"center"
			},
			effect:{
				type:String,
				default:'slide'
			},
			loop:{
				type:Boolean,
				default:true
			},
			direction:{
				type:String,
				default:'horizontal'
			},
			autoplay:{
				type:Number,
				default:0
			},
			paginationType:{
				type:String,
				default:'bullets'
			},
			slidesPerView:{
				type:Number,
				default:1
			},
			spaceBetween:{
				type:Number,
				default:0
			},
			autoplayDisableOnInteraction:{
				type:Boolean,
				default:false
			},
			freeMode:{
				type:Boolean,
				default:true
			},
			initialSlide:{
				type:Number,
				default:0
			},
			boolean:{
				type:Boolean,
				default:true
			},
			observer:{
				type:Boolean,
				default:true
			},
			observeParents:{
				type:Boolean,
				default:true
			}

		},
		//
		mounted:function(){
			var That = this;
			new Swiper('.'+That.swipeid,{
				//
				loop:That.loop,
				//
				pagination:'.swiper-pagination',
				//
				paginationType:That.paginationType,
				//
				autoplay:That.autoplay,
				//
				direction:That.direction,
				//
				effect:That.effect,

				slidesPerView:That.slidesPerView,

				observer:That.observer,

				observeParents:That.observeParents,
				//,false
				autoplayDisableOnInteraction:false,
				freeMode:That.freeMode,
				initialSlide:That.initialSlide,//free
				boolean:That.boolean


			})
		}
	}
</script>



<style type="text/css">
@import '../assets/libs/swiper/css/swiper.min.css';
.swiper-container img{width: 100%}
.swiper-pagination-bullet-active {background: #fff;}
</style>


